import React from 'react';
import styled, { keyframes } from 'styled-components';
// import './styles.css';
const MainWrapper = styled.div`
  /* background: red; */
  width: 16.5rem;
  height: 5.31rem;
  position: relative;
`;
const AlertLeft = keyframes`
 0% {  
  box-shadow: 0px 0px 1px 2px black;
  -webkit-box-shadow: 0px 0px 1px 2px black;
  -moz-box-shadow:0px 0px 1px 2px black;
  background:red;
   }
 25% {  
  background:red;
  box-shadow: 0px 0px 1px 2px yellow;
  -webkit-box-shadow: 0px 0px 1px 2px yellow;
  -moz-box-shadow:0px 0px 1px 2px yellow;
  }
 50% { 
  background:red;
  box-shadow: 0px 0px 1px 2px black;
  -webkit-box-shadow: 0px 0px 1px 2px black;
  -moz-box-shadow:0px 0px 1px 2px black;
  }
 75% {  
  background:red;
  box-shadow: 0px 0px 1px 2px yellow;
  -webkit-box-shadow: 0px 0px 1px 2px yellow;
  -moz-box-shadow:0px 0px 1px 2px yellow;
  }
 99% {  
  background:red;
  box-shadow: 0px 0px 1px 2px black;
  -webkit-box-shadow: 0px 0px 1px 2px black;
  -moz-box-shadow:0px 0px 1px 2px black;
  }

`;
const AlertRight = keyframes`
 15% {  
   box-shadow: 0px 0px 1px 2px black;
  -webkit-box-shadow: 0px 0px 1px 2px black;
  -moz-box-shadow:0px 0px 1px 2px black;
   background:red;
   }
 50% {  
   
  background:red;
  box-shadow: 0px 0px 1px 2px yellow;
  -webkit-box-shadow: 0px 0px 1px 2px yellow;
  -moz-box-shadow:0px 0px 1px 2px yellow;

  }
 100% { 
  background:red;
  box-shadow: 0px 0px 1px 2px black;
  -webkit-box-shadow: 0px 0px 1px 2px black;
  -moz-box-shadow:0px 0px 1px 2px black;
  }
`;

const Hold = keyframes`
100%{
  background:black;
  box-shadow: none;
}
`;
const Left = styled.div`
  background: red;
  width: 0.5rem;
  height: 0.5rem;
  position: absolute;
  top: 0.7rem;
  left: 2.05rem;
  border-radius: 50%;
  z-index: 99;
  animation: ${AlertLeft} 0.9s forwards infinite, ${Hold} 2s forwards infinite;

`;
const Right = styled.div`
  background: red;
  width: 0.5rem;
  height: 0.5rem;
  position: absolute;
  top: 0.7rem;
  left: 14.95rem;
  border-radius: 50%;
  z-index: 99;
  animation: ${AlertRight} 0.6s forwards infinite, ${Hold} 2s forwards infinite;
  animation-delay: 1s;
`;



