import React from 'react';
import styled, { css } from 'styled-components';

const LogoRapper = styled.div`
  cursor: pointer;
  width: 6rem;
  height: 4rem;

  @media (max-width: 768px) {
    ${({ desktop }) => {
      if (desktop === 'desktop')
        return css`
          display: none;
          border: none;
        `;
    }}
    ${({ mobile }) => {
      if (mobile === 'mobile')
        return css`
          width: 6.5rem;
          position: absolute;
          left: 1rem;
          top: 80vh;
          z-index: 5;
        `;
    }}
  }
  @media (min-width: 768px) {
    ${({ mobile }) => {
      if (mobile === 'mobile')
        return css`
          display: none;
          border: none;
        `;
    }}

    ${({ desktop }) => {
      if (desktop === 'desktop')
        return css`
          margin: 1em 2em 1em 2em;
          display: flex;
        `;
    }}
  }
`;

